body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body, html {
  height: 100%;
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.home {
  display: block;
  position: relative;
}


.header {
	color: white;
	display: inline-block;
	font-size: 300%;
	-webkit-padding-start: 0.5em;
	        padding-inline-start: 0.5em;
	-webkit-padding-end: 0.5em;
	        padding-inline-end: 0.5em;

	margin-bottom: 0.5em;
	border-style: solid;
	border-color: white;
	border-radius: 0.2em;
}

.details > p {
	color: white;
	font-size: 150%;
}

.details a {
	color: whitesmoke;
}
.profile  {
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	justify-content: center;
	
	
}

.profile > img {
	width: 200px;
	height: 200px;
	border-radius: 1em 0 0 1em;
	
}
.about-me {
	width:40%;
	height: 200px;
	padding: 0 4em;
	color: white;
	background-color: rgba(0, 0, 0, 0.2);
	border-radius: 0 1em 1em 0;
}
.about-me > h2 {
	-webkit-font-feature-settings: "c2sc";
	        font-feature-settings: "c2sc";
	font-variant-caps: small-caps;
	text-align: left;
	border-bottom: 2px solid white;
}

.about-me > p {
	text-align: justify;
	font-weight: 800;
	margin: 20px 0px
}

.icon-container{
	display: inline-flex;
	margin: 1em auto;
	padding: 0.3em 0.7em;
	border-radius: 0.5em;
}

.icon-container:hover{
	background-color: rgba(255, 255, 255, 0.2)
}

.icon {
	color: white;
	padding-left: 0.7em;
} 


.icon:hover {
	color: #222;
	
}
.icon-container .icon:first-child {
	padding-left: 0;
}

.react-photo-gallery--gallery {
    position: relative;
    overflow-y: scroll;
    height: 65vh;;
    margin-bottom: 5%;
}
.react-photo-gallery--gallery::-webkit-scrollbar { /* WebKit */
    width: 0;
    height: 0;
}
.code-container {
	flex-flow: row wrap;
	justify-content: flex-start;
	position: relative;
    overflow-y: scroll;
    height: 65vh;
    margin-bottom: 5%;
}

iframe {
	margin:auto;
	overflow: visible;
	border:none;
	padding-left: 1em;
}


iframe:first-child {
	padding-left: 0;
}
div[id$="container"] {
	background-color: white;
	border-radius: 8px 8px 0px 0px;
	margin-bottom: 0;
}
.resume-container {
	flex-flow: row wrap;
	justify-content: flex-start;
}
.resume-child {
	height: inherit;
	margin: auto;
	

}

.pdf-container {
	overflow-y: scroll;
	height: 75vh;
}

.download {
	color: whitesmoke;
	padding: 0.2em;
	display: inline-block;
	vertical-align: top;
	background: #4f4f4f;
	border-radius: 0px 8px 8px 0px;
}
.download:hover {
	background: whitesmoke;
	color: #4f4f4f;
}

canvas {
	min-width: 200px;
}

@media screen and (max-width: 720) {
	.icon {
		border-radius: 8px;
	}
}

img.bg {
	/* Set rules to fill background */
	min-height: 100%;
	min-width: 1024px;

	/* Set up proportionate scaling */
	width: 100%;
	height: auto;

	/* Set up positioning */
	position: fixed;
	top: 0;
	left: 0;

	cursor: pointer;
}

div.content {
	width: 100%;
	position: absolute;
	text-align: center;
	margin-top: 32px;
	margin-bottom: 32px;
}

.pointer {
	cursor: pointer;
}

@media screen and (max-width: 1024px) {
	/* Specific to this particular image */
	img.bg {
		left: 50%;
		margin-left: -512px; /* 50% */
	}
}



